<template>
  <el-container style="height: 100vh">
    <el-header style="background-color: #B3C0D1;">
      <div style="text-align: left; float: left; line-height: 60px; font-style: italic; font-weight: 400; font-size: 25px; letter-spacing: 0.478em;">
        <span>校友录系统</span>
      </div>
    </el-header>
    <el-main style="padding: 0px">
      <el-container style="height: 90vh;">
        <el-aside style="width: 220px; background-color: rgb(238, 241, 246)">
          <el-menu router :default-openeds="['1','2']">
        <el-submenu index="1">
          <template slot="title"><i class="el-icon-menu"></i>用户信息管理</template>
            <el-menu-item index="/administrator">用户列表</el-menu-item>
            <!-- <el-menu-item index="1-2">用户信息查询</el-menu-item> -->
        </el-submenu>
        <el-submenu index="2">
          <template slot="title"><i class="el-icon-menu"></i>系统信息管理</template>
            <el-menu-item index="/newsmanage">新闻管理</el-menu-item>
            <!-- <el-menu-item index="2-2">公告</el-menu-item> -->
            <el-menu-item index="/messagemanage">留言管理</el-menu-item>
            <el-menu-item index="/logmanage">日志管理</el-menu-item>
            <!-- <el-menu-item index="2-6">用户信息修改</el-menu-item> -->
        </el-submenu>
      </el-menu>
        </el-aside>
        <el-container>
          <el-header style="text-align: right; font-size: 12px">
            <el-dropdown>
              <i class="el-icon-setting" style="margin-right: 15px"></i>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>查看</el-dropdown-item>
                <el-dropdown-item>新增</el-dropdown-item>
                <el-dropdown-item>删除</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <span>王小虎</span>
          </el-header>

          <el-main>
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span>新闻列表</span>
                <el-button style="margin-left: 650px" @click="dirct" type="primary" round>创建新闻</el-button>
              </div>
              <div>
                <el-table
                  :data="tableData"
                  style="width: 100%">
                  <el-table-column
                    label="日期"
                    width="180">
                    <template slot-scope="scope">
                      <i class="el-icon-time"></i>
                      <span style="margin-left: 10px">{{ scope.row.timestamp }}</span>
                    </template>
                  </el-table-column>
                  <el-table-column
                    label="标题"
                    width="500">
                    <template slot-scope="scope">
                      <el-popover trigger="hover" placement="top">
                        <p>标题: {{ scope.row.title }}</p>
                        <p>内容: {{ scope.row.content }}</p>
                        <p>发布人: {{ scope.row.author }}</p>
                        <div slot="reference" class="name-wrapper">
                          <el-tag size="medium">{{ scope.row.title }}</el-tag>
                        </div>
                      </el-popover>
                    </template>
                  </el-table-column>
                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <el-button
                        size="mini"
                        @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                      <el-button
                        size="mini"
                        type="danger"
                        @click="handleDelete(scope.row._id)">删除</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </el-card>
          </el-main>
        </el-container>
      </el-container>
    </el-main>
  </el-container>
</template>

<style>
  .el-header {
    color: #333;
    line-height: 60px;
  }

  .el-card {
    width: 85%;
    margin: auto;
  }

  .el-aside {
    color: #333;
  }
</style>
<script>
export default {
  data () {
      return {
      tableData: []
    }
  },
  methods: {
    dirct() {
      this.$router.push({ path: '/createNew' })
    },
    fetch() {
      this.$http.get('news').then( res => {
        this.tableData = res.data
        // console.log(res.data[0].src)
      })
    },
    handleDelete(id) {
      this.$http.delete(`news/${id}`).then( res => {
        if (res.data.status === 1){
            this.$message({
            message: '删除成功!',
            type: 'success'
          });
          this.fetch()
        }
      })
    }
  },
  created() {
    this.fetch()
  }
}
</script>